import React, { Suspense } from "react";
import { OrbitControls } from "@react-three/drei";
import { Canvas } from "@react-three/fiber";
import { Model } from "./Model";

const CanvasObject = React.memo(
  React.forwardRef(({ item, currentColor }, ref) => {
    const [scroll, setScroll] = React.useState(false);

    return (
      <Canvas
        resize={{ scroll: false }}
        style={{ height: "140px" }}
        camera={{
          fov: 10,
          near: 0.1,
          far: 5000,
          position: item.cameraPosition,
          rotation: item.cameraRotation,
        }}
        onPointerDown={(e) => setTimeout(() => setScroll(true), 250)}
        onPointerOut={(e) => setScroll(false)}>
        <ambientLight intensity={0.5} />
        <rectAreaLight
          width={400}
          height={400}
          intensity={0.75}
          color={"white"}
          position={[-165, 100, 0]}
          rotation={[0, 180, 0]}
          castShadow
        />
        <Suspense fallback={null}>
          <Model
            model={item.model}
            color={currentColor.hex}
            scale={item.scale ? item.scale : [0.1, 0.1, 0.1]}
            rotation={item.rotation}
          />
        </Suspense>
        <OrbitControls ref={ref} enableZoom={scroll} enableRotate={scroll} />
      </Canvas>
    );
  })
);

export default CanvasObject;
